/**
 * React hooks for the Hygraph Inspector SDK
 * Provides convenient hooks for common Inspector operations
 */

import { useEffect, useCallback, useRef } from 'react';
import { useInspector, useInspectorSave, useInspectorEvent } from './HygraphInspector';

/**
 * Hook for framework-specific refresh integration
 * Automatically detects the framework and provides appropriate refresh
 */
export function useInspectorRefresh(): {
  refresh: () => void | Promise<void>;
  framework: string | null;
} {
  const { inspector } = useInspector();
  const frameworkRef = useRef<string | null>(null);

  // Detect framework on mount
  useEffect(() => {
    if (!inspector) return;

    const integration = (inspector as any).frameworkIntegration;
    const framework = integration?.getFramework();
    frameworkRef.current = framework?.type || null;
  }, [inspector]);

  const refresh = useCallback(async () => {
    if (!inspector) return;

    const integration = (inspector as any).frameworkIntegration;
    if (integration) {
      await integration.refresh();
    } else {
      window.location.reload();
    }
  }, [inspector]);

  return {
    refresh,
    framework: frameworkRef.current,
  };
}

/**
 * Hook for Next.js router integration
 * Provides automatic refresh using Next.js router.replace
 */
export function useInspectorNextjs(): void {
  const { refresh } = useInspectorRefresh();

  // Try to get Next.js router
  const getNextRouter = useCallback(() => {
    if (typeof window === 'undefined') return null;

    // Try multiple ways to access Next.js router
    const router =
      (window as any).next?.router ||
      (window as any).__NEXT_DATA__?.router ||
      null;

    return router;
  }, []);

  const nextjsRefresh = useCallback(() => {
    const router = getNextRouter();
    if (router && typeof router.replace === 'function') {
      router.replace(router.asPath || window.location.pathname);
    } else {
      refresh();
    }
  }, [refresh, getNextRouter]);

  useInspectorSave(nextjsRefresh);
}

/**
 * Hook for Remix integration
 * Provides automatic refresh using Remix revalidator
 */
export function useInspectorRemix(): void {
  const { refresh } = useInspectorRefresh();

  const getRemixRevalidator = useCallback(() => {
    if (typeof window === 'undefined') return null;

    return (
      (window as any).__remixRevalidator ||
      (window as any).__remixRouterContext?.revalidator ||
      null
    );
  }, []);

  const remixRefresh = useCallback(() => {
    const revalidator = getRemixRevalidator();
    if (revalidator && typeof revalidator.revalidate === 'function') {
      revalidator.revalidate();
    } else {
      refresh();
    }
  }, [refresh, getRemixRevalidator]);

  useInspectorSave(remixRefresh);
}

/**
 * Hook for field update events
 * Provides callbacks for when fields are updated
 */
export function useInspectorFieldUpdates(
  onUpdate?: (update: { entryId: string; fieldApiId: string; newValue: any }) => void,
  onError?: (error: { entryId: string; fieldApiId: string; error: string }) => void
): void {
  useInspectorEvent('inspector:field-updated', (event) => {
    onUpdate?.(event.detail);
  });

  useInspectorEvent('inspector:update-failed', (event) => {
    onError?.(event.detail);
  });
}

/**
 * Hook for Inspector connection status
 * Provides current connection state and callbacks
 */
export function useInspectorConnection(): {
  isConnected: boolean;
  isReady: boolean;
  mode: 'iframe' | 'standalone' | null;
} {
  const { inspector, isReady, isConnected } = useInspector();

  const mode = inspector?.getMode() || null;

  return {
    isConnected,
    isReady,
    mode,
  };
}

/**
 * Hook for manual Inspector operations
 * Provides methods to manually control the Inspector
 */
export function useInspectorActions(): {
  refresh: () => void;
  destroy: () => void;
  getVersion: () => string | null;
  getMode: () => 'iframe' | 'standalone' | null;
} {
  const { inspector } = useInspector();

  const refresh = useCallback(() => {
    inspector?.refresh();
  }, [inspector]);

  const destroy = useCallback(() => {
    inspector?.destroy();
  }, [inspector]);

  const getVersion = useCallback(() => {
    return inspector?.getVersion() || null;
  }, [inspector]);

  const getMode = useCallback(() => {
    return inspector?.getMode() || null;
  }, [inspector]);

  return {
    refresh,
    destroy,
    getVersion,
    getMode,
  };
}

/**
 * Hook for debugging and development
 * Provides debugging information and utilities
 */
export function useInspectorDebug(): {
  inspector: any;
  stats: any;
  registryKeys: string[];
  framework: any;
} {
  const { inspector } = useInspector();

  const getStats = useCallback(() => {
    if (!inspector) return null;
    const fieldRegistry = (inspector as any).fieldRegistry;
    return fieldRegistry?.getStats() || null;
  }, [inspector]);

  const getRegistryKeys = useCallback(() => {
    if (!inspector) return [];
    const fieldRegistry = (inspector as any).fieldRegistry;
    return fieldRegistry?.getRegistryKeys() || [];
  }, [inspector]);

  const getFramework = useCallback(() => {
    if (!inspector) return null;
    const frameworkIntegration = (inspector as any).frameworkIntegration;
    return frameworkIntegration?.getFramework() || null;
  }, [inspector]);

  return {
    inspector,
    stats: getStats(),
    registryKeys: getRegistryKeys(),
    framework: getFramework(),
  };
}